.overList {
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  overflow: hidden;
}
.wrapperList {
  width: 250px;
  padding: 10px;
  height: 320px;
  background: transparent;

  @media screen and (min-width: 480px) {
    padding: 20px;
    width: 400px;
    backdrop-filter: blur(10px);
  }

  overflow-y: scroll;
  scroll-behavior: smooth;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: rgb(0, 0, 0);
    border-radius: 10px;
  }
}

.item {
  width: 100%;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__text {
    font-family: inherit;
    font-weight: 400;
    font-size: 18px;
    color: rgb(0, 0, 0);
    white-space: nowrap;
    max-width: 150px;
    overflow: hidden;
    width: 100%;
    padding: 0 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  &__button {
    font-family: inherit;
    font-size: 18px;
    font-weight: 700;
    color: rgb(0, 0, 0);
    padding: 5px 10px;

    border: 2px solid rgb(0, 0, 0);
    outline: none;
    background-color: transparent;

    border-radius: 6px;
    cursor: pointer;
    transition: 0.5s;

    &:hover {
      background-color: rgb(0, 0, 0);
      color: black;
    }

    @media screen and (max-width: 479px) {
      font-size: 16px;
      font-weight: 600;
      padding: 3px 8px;
    }
  }
}
